.upcoming-game {
  /* background-color: rgba(0, 0, 0, 0.35); */
  background-color: #ce2f2f4d;
  border-radius: 10px;

  min-height: 250px;
  width: auto;

  display: flex;
  flex-direction: row;

  align-items: center;
  justify-content: center;

  transform: translateY(80px);

  margin-bottom: 15px;
  overflow: hidden;

  opacity: 0;
  transition: 0.2s;
}

/* .upcoming-game:hover {
  transform: scale(1.05, 1.05);
} */

.fillDivUPG {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
  text-decoration: none;
}

.ug-img {
  border-radius: 5px;

  height: 230px;
  width: 350px;
  background-image: url(./games/hack-banner-700x459.png);
  background-repeat: no-repeat;
  background-size: cover;

  margin-left: 9px;

  transition: 0.2s;
}

.upcoming-game.fade-in {
  opacity: 1;
  transform: translateY(0);

  transform: scale(1, 1);
  transition: 0.3s;
}

.fade-in:hover {
  transform: scale(1.02, 1.02);
  box-shadow: -4px 4px 4px -4px rgba(0, 0, 0, 0.5),
    0px 0px 18px 0px rgba(0, 0, 0, 0.8);
}

.ug-text {
  color: white;
  height: 230px;
  width: auto;
  /* background-color: rgba(238, 130, 238, 0.1); */

  display: flex;
  flex-direction: column;
  flex-wrap: wrap;

  align-items: flex-start;
  justify-content: flex-start;
}

.ug-text h1 {
  color: white;
  margin-left: 9px;
  margin-right: 7px;
  margin-bottom: 15px;

  font-weight: bold;
  font-size: 20px;

  transition: 0.2s;
}

.ug-text a:hover {
  color: #dbdbdb;
}

.ug-text p {
  color: rgb(224, 223, 223);
  margin-left: 9px;
  margin-right: 7px;
}

.ug-text .rls-date {
  justify-content: flex-end;
  align-items: flex-end;
  color: rgb(224, 223, 223);
  font-size: 14px;

  margin-top: 10px;
}

@media screen and (max-width: 880px) {
  .fillDivUPG {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
    text-decoration: none;
  }

  .upcoming-game {
    flex-direction: column;

    align-items: center;
    justify-content: center;
  }

  .ug-img {
    height: 230px;
    width: 500px;
    margin-left: 0px;
  }

  .ug-text {
    height: auto;
    margin-top: 8px;
    margin-bottom: 8px;
  }
}

@media screen and (max-width: 550px) {
  .fillDivUPG {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
    text-decoration: none;
  }

  .upcoming-game {
    width: auto;
    flex-direction: column;

    align-items: center;
    justify-content: center;
  }

  .ug-img {
    height: 230px;
    width: 95vw;
    margin-left: 0px;
    transition: 0s;
  }

  .ug-text {
    width: auto;
    height: auto;
    margin-top: 8px;
    margin-bottom: 8px;
  }
}
