.about-inner {
  width: 700px;
  display: flex;
  flex-direction: column;
}

.about-text-div {
  width: 700px;
  display: flex;
  flex-direction: row;
}

.about h1 {
  color: #ebebeb;
  margin-bottom: 30px;
  font-size: 45px;
}

.about p {
  color: #cccccc;
  margin-left: 15px;
  margin-right: 15px;
}

@media screen and (max-width: 768px) {
  .about {
    margin-bottom: -37px;
  }

  .about a {
    font-size: 30px;
    margin-top: 10px;
    display: block;
    color: rgb(218, 218, 218);
    text-decoration: none;
  }

  .about-inner {
    width: 700px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 13px;
  }

  .about-text-div {
    width: 300px;
    display: flex;

    flex-direction: column;
  }

  .about p {
    margin-top: 15px;
    margin-bottom: 15px;
  }

  .about h1 {
    margin-top: 15px;
  }
}
