.hackintosh {
  font-family: Verdana, Geneva, Tahoma, sans-serif;

  background: #0f2027; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to bottom,
    #193957,
    #1a2f41,
    #0f1927
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to bottom,
    #16314b,
    #162736,
    #0b111b
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  height: 100vh;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  overflow: hidden;
}

.topBar {
  z-index: 99;
  position: absolute;
  /* background-color: rgba(0, 0, 0, 0.5); */
  min-height: 50px;
  width: 100%;
  top: 0;
  left: 0;

  display: flex;
  flex-direction: column;
  justify-content: center;
}

.backButton {
  width: 200px;
  height: 45px;
}

.fillDiv {
  display: flex;
  flex-direction: row;
  align-items: center;

  height: 100%;
  width: 100%;
  text-decoration: none;
}

.fillDiv h1 {
  color: white;
  font-weight: 100;
  font-size: 20px;

  transition: 0.1s;
}

.fillDiv:hover h1 {
  color: rgb(168, 199, 219);
}

.backButtonSvg {
  background-image: url(./games/hackintosh/back.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: 20px;

  width: 20px;
  height: 20px;

  margin-left: 10px;
  margin-right: 10px;
}

.uiBorder {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  justify-items: center;

  background: rgba(0, 0, 0, 0.3);

  min-height: 300px;
  border-style: solid;
  border-color: rgb(11, 148, 212);
  border-width: 2px;
  box-shadow: 0px 0px 3px 1px rgb(11, 148, 212),
    inset 0px 0px 3px 1px rgb(11, 148, 212);

  margin-left: 20px;
  margin-right: 20px;
  margin-top: 30px;

  transform: skewX(-7deg);
}

.uiBorder h1 {
  pointer-events: none;
  font-size: 50px;
  font-weight: 100;
  color: rgb(13, 166, 236);
  text-shadow: 0px 0px 5px rgb(13, 166, 236);

  margin-left: 80px;
  margin-right: 80px;

  transform: skewX(7deg);
}

.uiBorder h2 {
  pointer-events: none;
  font-size: 23px;
  font-weight: 100;
  color: rgb(207, 27, 27);
  text-shadow: 0px 0px 5px rgb(207, 27, 27);

  margin-top: 30px;
  margin-left: 50px;
  margin-right: 50px;
  transform: skewX(7deg);

  animation: comingDate 3s infinite;
  -webkit-animation: comingDate 3s infinite;
}

.uiBorder h1::after {
  content: "_";
  animation: underline 1s infinite step-end;
  -webkit-animation: underline 1s infinite step-end;
}

@keyframes underline {
  0% {
    opacity: 0;
  }

  50% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

@keyframes comingDate {
  0% {
    color: rgb(207, 27, 27);
  }

  50% {
    color: rgb(145, 20, 20);
  }

  100% {
    color: rgb(207, 27, 27);
  }
}

.about {
  z-index: 1;
  position: relative;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;

  border-top: rgb(13, 166, 236) solid 4px;
  background: rgba(0, 0, 0, 0.3);
}

.aboutInner {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.about h1 {
  color: rgb(13, 166, 236);
  text-shadow: 0px 0px 5px rgb(13, 166, 236);
  font-weight: 100;

  margin-top: 10px;
  margin-bottom: 10px;
  margin-left: 15px;
  margin-right: 15px;
}

.aboutInner h1 {
  color: rgb(155, 190, 253);
  font-size: 23px;
  text-shadow: 0px 0px 0px;
  margin-bottom: 10px;
  margin-top: 0px;
}

.aboutInner p {
  color: rgb(155, 190, 253);
}

.divider1 {
  display: flex;

  align-items: center;
  justify-content: center;

  width: 100%;
  min-height: 300px;
  background-repeat: no-repeat;
  background-size: cover;
  margin-bottom: 30px;
  margin-top: 30px;
}
