body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.nav-bar {
  z-index: 99;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.5);
  min-height: 20px;
  width: 100%;
  top: 0;
  left: 0;

  display: flex;
  flex-wrap: wrap;

  align-items: center;
  align-content: center;
  justify-content: center;
}

/*LOGO*/
.nav-bar img {
  z-index: 100;
  margin-right: 10px;
  margin-left: 5px;
  height: 70px;
  width: 70px;
}

.nav-button {
  margin-left: 15px;
  z-index: 1;

  pointer-events: all;

  margin-top: 7px;
  margin-bottom: 7px;

  display: flex;

  justify-content: center;
}

.nav-button a {
  padding: 22px 5px;

  color: white;
  font-weight: 400;
  text-decoration: none;

  font-size: 15px;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}

.nav-button a:hover {
  font-size: 17px;
  color: #dbdbdb;
}

.smooth-link {
  cursor: pointer;
}

/*NAV ACTIVE*/
.nav-button-act {
  margin-left: 15px;
  z-index: 1;

  background-color: white;
  padding: 13px 15px 13px 13px;
  border-radius: 6px;
}

.nav-button-act a {
  z-index: 100;
  color: black;
  font-weight: bold;
  text-decoration: none;
}

.nav-button-act p {
  color: black;
  font-weight: bold;
  pointer-events: none;
}

.dropDownContent {
  display: none;
  margin-top: 60px;
  position: absolute;
  background-color: #4e4e4e;
  min-width: 160px;
  z-index: 1;
  opacity: 0;

  border-top: white 3px solid;

  -webkit-transform: translateY(-30px);

          transform: translateY(-30px);

  -webkit-transition: 0.2s;

  transition: 0.2s;
}

.nav-button:hover .dropDownContent {
  display: inline;
  -webkit-transform: translateY(0);
          transform: translateY(0);
  opacity: 1;
}

.dropDownContent a {
  color: white;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

