.social-media {
  background-image: url(./media/sitetheme.png);
  background-color: #ce2f2f;
  position: relative;
  min-height: 400px;

  display: flex;
  flex-direction: column;

  justify-content: center;
  align-items: center;
  align-content: center;
  text-align: center;

  /* box-shadow: -4px 4px 4px -4px rgba(0, 0, 0, 0.5),
    0px 0px 18px 0px rgba(0, 0, 0, 0.8); */
}

.social-contact-container {
  display: flex;
  flex-direction: column;
}

.social-media-inner h1 {
  color: white;
  font-size: 45px;

  margin-top: 60px;
}

.social-media-button {
  margin-top: 20px;
  vertical-align: middle;
  display: inline-block;
}

.social-twitter {
  background-image: url(./media/social-media/twitter.svg);
  background-position: center;
  background-repeat: no-repeat;

  vertical-align: middle;
  padding-left: 15px;
  display: inline-block;

  color: white;

  margin: 0px 9px 0px 9px;
}

.social-facebook {
  background-image: url(./media/social-media/facebook.svg);
  background-position: center;
  background-repeat: no-repeat;

  vertical-align: middle;
  padding-left: 15px;
  display: inline-block;

  color: white;

  margin: 0px 9px 0px 9px;
}

.social-instagram {
  background-image: url(./media/social-media/instagram.svg);
  background-position: center;
  background-repeat: no-repeat;

  vertical-align: middle;
  padding-left: 15px;
  display: inline-block;

  color: white;

  margin: 0px 9px 0px 9px;
}

.social-youtube {
  background-image: url(./media/social-media/youtube.svg);
  background-position: center;
  background-repeat: no-repeat;

  vertical-align: middle;
  padding-left: 15px;
  display: inline-block;

  color: white;

  margin: 0px 9px 0px 9px;
}

.social-btn {
  transform: scale(1, 1);
  transition: 0.2s;

  width: 30px;
  height: 30px;

  background-size: 30px;
}

.social-btn:hover {
  transform: scale(1.15, 1.15);
}

.contact-inner h1 {
  color: white;
  text-align: center;
  font-size: 45px;
  margin-bottom: 20px;
}

.contact-inner a {
  display: flex;
  flex-direction: column;
  margin-top: 10px;
  color: rgb(218, 218, 218);
  text-decoration: none;

  transition: 0.2s;
}

.contact-inner a:hover {
  color: #b3b3b3;
}

@media screen and (max-width: 768px) {
  .social-btn {
    transform: scale(1, 1);
    transition: 0.2s;

    width: 50px;
    height: 50px;

    background-size: 50px;
  }
}
