* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: sans-serif;
}

.gamesIntro {
  height: 60vh;
}

.gamesIntro img {
  pointer-events: none;
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.gamesIntro h1 {
  pointer-events: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 80px;
  color: white;
}

.gamesBody {
  display: flex;
  flex-direction: column;
  align-content: center;
  align-items: center;
  justify-content: center;

  position: relative;
  background-image: url(./media/sitetheme2.png);
  background-color: #1c1c1c;
  min-height: 700px;

  box-shadow: -4px 4px 4px -4px rgba(0, 0, 0, 0.5),
    0px 0px 18px 0px rgba(0, 0, 0, 0.8);
}

.gamesBody h1 {
  color: white;
  font-size: 5vh;
  margin-left: 5px;
  margin-right: 5px;
  /* margin-bottom: 60px; */
  margin-top: 15px;
  margin-bottom: 50px;
}

.gamesItem {
  z-index: 1;
  height: 370px;
  width: 270px;
  background-color: #a5a5a5;
  border-radius: 5px;
  overflow: hidden;
  margin-right: 9px;

  margin-left: 9px;
  margin-top: 18px;

  transform: translateY(0);

  transition: 0.2s;
}

.gamesItem h1 {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, 10px);

  color: rgb(255, 255, 255);
  font-size: 30px; /* -webkit-text-stroke: 1px black; */
  width: max-content;
  opacity: 0;
  transition: 0.2s;
}

.gamesItem p {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, 50px);

  color: rgb(255, 255, 255);
  font-size: 15px; /* -webkit-text-stroke: 1px black; */
  width: max-content;
  opacity: 0;
  transition: 0.2s;
}

.gamesItem:hover {
  transform: scale(1.05, 1.05);
  box-shadow: -4px 4px 4px -4px rgba(0, 0, 0, 0.5),
    0px 0px 18px 0px rgba(0, 0, 0, 0.8);
}

.games {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-content: center;
}

@media screen and (max-width: 880px) {
  .gamesBody {
    /* padding-top: 110px;
      padding-bottom: 110px;
      position: relative; */
    display: flex;
    flex-direction: column;
  }
}
