* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: sans-serif;
}

.intro {
  height: 100vh;

  /*    background: url(KEBABSKI%202.mp4) no-repeat;*/
}

/*.intro img{
    height: 100%;
    width: 100%;
    object-fit: cover;
}*/

#bg-video {
  position: fixed;
  right: 0;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;
  pointer-events: none;
}

.intro h1 {
  pointer-events: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 80px;
  color: white;
}

.title-bar {
  box-shadow: -4px 4px 4px -4px rgba(0, 0, 0, 0.5),
    0px 0px 18px 0px rgba(0, 0, 0, 0.8);
  position: relative;
  min-height: 7vh;
}

.site-body {
  display: flex;
  flex-direction: column;
  align-content: center;
  align-items: center;
  justify-content: center;

  position: relative;
  background-image: url(./media/sitetheme2.png);
  background-color: #1c1c1c;
  min-height: 700px;

  box-shadow: -4px 4px 4px -4px rgba(0, 0, 0, 0.5),
    0px 0px 18px 0px rgba(0, 0, 0, 0.8);
}
.site-body h1 {
  color: white;
  font-size: 5vh;
  margin-left: 5px;
  margin-right: 5px;
  /* margin-bottom: 60px; */
  margin-top: 15px;
  margin-bottom: 15px;
}
.games {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-content: center;
}
.item {
  z-index: 1;
  height: 370px;
  width: 270px;
  background-color: #a5a5a5;
  border-radius: 5px;
  overflow: hidden;
  margin-right: 9px;

  margin-left: 9px;
  margin-top: 18px;
  /* margin-bottom: 18px; */
  transform: translateY(80px);
  transition: 0.2s;
  opacity: 0;
}
.item h1 {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, 10px);

  color: rgb(255, 255, 255);
  font-size: 30px; /* -webkit-text-stroke: 1px black; */
  width: max-content;
  opacity: 0;
  transition: 0.2s;
}
.item p {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, 50px);

  color: rgb(255, 255, 255);
  font-size: 15px; /* -webkit-text-stroke: 1px black; */
  width: max-content;
  opacity: 0;
  transition: 0.2s;
}
a.fill-div-games {
  display: block;
  height: 100%;
  width: 100%;
  text-decoration: none;
}
.fill-div-games {
  transition: 0.2s;
}
.fill-div-games:hover {
  background-color: #00000080;
}

.fill-div-games:hover h1 {
  opacity: 1;
  transform: translate(-50%, -50%);
}
.fill-div-games:hover p {
  opacity: 1;
  transform: translate(-50%, 170%);
}
.item.fade-in {
  opacity: 1;
  transform: translateY(0);
  transform: scale(1, 1);
  transition: 0.3s;
}

#item01 {
  background: url(./games/kebab-banner.png) no-repeat;
  background-size: cover;
}
#item02 {
  background: url(./games/dontslip-banner.png) no-repeat;
  background-size: cover;
}
#item03 {
  background: url(./games/hack-banner.png) no-repeat;
  background-size: cover;
}
.item:hover {
  transform: scale(1.05, 1.05);
  box-shadow: -4px 4px 4px -4px rgba(0, 0, 0, 0.5),
    0px 0px 18px 0px rgba(0, 0, 0, 0.8);
}
.bid-button {
  /* margin-top: 20px; */
  width: 130px;
  height: 45px;
  margin-top: 25px;
  margin-bottom: 25px;
  border-radius: 4px;
  border-style: solid;
  border-color: white;
  border-width: 2px;
  transition: 0.2s;
}
.bid-button:hover {
  background-color: white;
}
.bid-button:hover a {
  color: black;
}

.bid-button a {
  text-align: center;
  color: white;
}
a.fill-div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  width: 100%;
  text-decoration: none;
}
.buttons-container {
  background-color: #919191;
  position: relative;
  padding-top: 50px;
  padding-bottom: 50px;
  height: 17vh;
  width: 100%;
}
.button {
  background-color: #a7bdc6;
  z-index: 1;
  width: 20%;
  height: 100%;
  border-color: black;
  border-radius: 3px;
  display: inline-block;
  margin-right: 30px;
}
.button a {
  color: white;
  text-align: center;
  padding-top: 17px;
  font-size: 30px;
}
.footer {
  z-index: 1;

  background-color: #1c1c1c;
  position: relative;
  display: flex;

  min-height: 50px;
  width: 100%;

  justify-content: center;
  align-items: center;

  box-shadow: -4px 4px 4px -4px rgba(0, 0, 0, 0.5),
    0px 0px 18px 0px rgba(0, 0, 0, 0.8);
}
.footer p {
  color: white;
  font-size: 15px;
}
.mask-colophon {
  width: 100%;
  background-image: url("./media/spacer-footer.png");
  background-position: center 0;
  background-repeat: no-repeat;
  position: relative;
  height: 37px;
  z-index: 7000;
  margin-top: -38px;
  padding-top: 10px;
}

@media screen and (max-width: 880px) {
  .games {
    /* padding-top: 110px;
    padding-bottom: 110px;
    position: relative; */
    display: flex;
    flex-direction: column;
    padding-bottom: 30px;
  }
}
